
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


import { mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
import axios from 'axios'
export default {
name: "create",
data() {
return {
title:'',
content:'',
toolbars: {
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 
superscript: true, // 
subscript: true, // 
imagelink: true, // 
fullscreen: true, // 
htmlcode: true, // html
help: true, // 
undo: true, // 
redo: true, // 
trash: true, // 
save: true, // eventssave
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
subfield: true, // 
preview: true, // 
}
}
},
components: {
XTextarea,
Group,
XButton,
mavonEditor
},
methods: {

uploadImage:function(pos,file){
var app=this;





var params = new FormData();
params.append('smfile',file);
axios.post('https://sm.ms/api/upload',params,{



headers:{'Content-Type':'multipart/form-data'}
}).then(function(response){


app.$refs.md.$img2Url(pos,response.data.data.url)
})

},

addtopic:function(){


var app=this;

this.$http.post('/topicks',{
title:this.title,
content:this.content,
tab:'dev',
accesstoken:
this.$store.state.accesstoken
}).then(function(response){

if (response.data && response.data.success){

app.$router.replace('/topiclist/dev');
}
})
}
},
};



